
.personal-info {
  overflow: hidden;
  padding: 10px;
  background-color: #F5F5DC;
}
.personal-icon {
  float: left;
  width: 50px;
  height: 50px;
}
.personal-icon>img{
  width: 100%;
  border-radius: 5px;
}
.personal-information{
  margin-left: 10px;
  float: left;
}
.personal-name {
  font-size: 18px;
  color: #6495ED;
  padding-bottom: 10px;
}
.personal-score {
  float: right;
  line-height: 50px;
  color: #FFB3FF;
}
.personal-nav {
  text-align: center;
  border-collapse:collapse;
  color: #888;
  background-color: #fff;
}
.personal-nav>tbody>tr>td {
  padding: 10px 0;
  border-bottom: 2px solid #e8e8e8;
}
.personal-active {
  color: #009FCC;
  border-bottom: 2px solid #009FCC!important;
}
.margin-top-20 {
  margin-top: 20px;
}



