
.height-10 {
  height: 10px;
}
.list {
  width: 100%;
  background-color: #e8e8e8;
  /*padding: 0 0 10px 0;*/
}
.list-cell {
  /*width: 100%;*/
  background-color: #fff;
  margin-top: 10px;
  padding: 0 8px;
  box-shadow: 5px 0 5px 0 #aaa;
}
.cell-title {
  margin: 0;
  padding: 5px 0;
  line-height: 22px;
}
.cell-msg {
  overflow: hidden;
}
.user-icon {
  width: 30px;
  height: 30px;
  float: left;
}
.user-icon>img {
  width: 100%;
  border-radius: 4px;
  display:inline-block;
}
.user-name {
  line-height: 30px;
  font-weight: 400;
  float: left;
  margin-left: 5px;
}
.cell-tab {
  line-height: 30px;
  float: right;
  color: #FFB3FF;
}
.cell-tab >span{
  color: #00AA55;
}



