
#menu {
  width: 170px;
  height: 100%;
  /*padding: 10px;*/
  background: #F8F8FF ;
  position: absolute;
  transition: all .25s ease;
  /*left: -270px;  !* test fixed + scroll, on retire la position top *!*/
}
.menu-enter,.menu-leave {
  transform: translateX(0px);
}
.menu-enter-active,.menu-leave-active {
  transform: translateX(-170px);
}
.menu-user {
  overflow: hidden;
  /*background-color: #fff;*/
  padding: 10px;
  background-color: #F5F5DC ;
}
.menu-userIcon {
  float: left;
  width: 30px;
  height: 30px;
  background: url("../assets/images/noUser.png") no-repeat center;
  background-size: 30px;
  border-radius: 4px;
}
.userName {
  margin-left: 10px;
  line-height: 30px;
  float: left;
  color: #6495ED ;
}
.menu-list {
}
.menu-list-cell {
  padding: 7px;
  /*margin-top: 10px;*/
  height: 30px;
  line-height: 30px;
}
.menu-active {
  background-color: #D3D3D3;
}
.home-icon:before,.collection-icon:before,.msg-icon:before {
  display: inline-block;
  content: '';
  width: 20px;
  height: 30px;
  vertical-align: middle;
  margin-right: 10px;
}
.home-icon:before {
  background: url("../assets/images/ViewGallery.png") no-repeat 0 5px;
  background-size: 20px;
}
.collection-icon:before {
  background: url("../assets/images/favorite.png") no-repeat 0 3px;
  background-size: 20px;
}
.msg-icon:before {
  background: url("../assets/images/email.png") no-repeat 0 3px;
  background-size: 20px;
}
.msgCount {
  float: right;
  color: coral;
}
.me {
  position: absolute;
  bottom: 10px;
  right: 10px;
  color: #009fcc;
}



