html,body{
    height: 100%;
    width: 100%;
}

html,body,p{
    padding: 0;
    margin: 0;
}

a {
    color: inherit;
    text-decoration: none;
}

body {
    font-size: 14px;
}

#header {
    height: 44px;
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    text-align: center;
    box-shadow:0 0 3px 0 #888;
    background-color: #fff;
    z-index: 99;
}

#header >span {
    line-height: 44px;
    font-size: 16px;
}

.left-icon,.right-icon {
    position: absolute;
    width: 44px;
    height: 44px;
    z-index: 10;
    top: 0;

}

#content {
    padding-top: 46px;
    position: relative;
    z-index: 0;
}

.col-33 {
    width: 33.33%;
    float: left;
}

.text-center {
    text-align: center;
}

.text-right {
    text-align: right;
}

.mask {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.3);
    z-index: 999;
}

.btn {
    border: 0;
}


