
.login-bg {
  height: 100%;
  background-color: #FFFAFA ;
}
.login-from {
  position: fixed;
  top:50%;
  width: 100%;
  margin-top: -60px;
  /*padding: 0 30px 0 30px;*/
}
.login-from >input,.login-from >button {
  display: block;
  width: 80%;
  padding: 0;
  margin: 0 auto;
  border-radius: 4px;
}
.login-from >input {
  height: 25px;
}
.login-from >button {
  height: 30px;
  margin-top: 15px;
  background-color: #20B2AA   ;
  color: #fff;
}



