
.write-article-box {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  overflow: hidden;
  background-color: #fff;
  transition: all .25s ease;
}
.article-enter,.article-leave {
  transform: translateX(0px);
}
.article-enter-active,.article-leave-active {
  transform: translateX(100%);
}
.write-header {
  margin-top: 46px;
  padding: 10px;
}
.write-title ,.write-tab {
  padding: 4px 8px;
  border-bottom: 1px solid #e8e8e8;
}
.write-title>input {
  border: 0;
  outline:none;
  font-size: 16px;
  width: 100%;
}
.gray {
  color: #888;
}
.write-tab>p {
  padding: 5px 0;
}
.write-tab>div{
  padding: 10px 0;
}
.write-tab span {
  color: #888;
  border: 1px solid #888;
  border-radius: 4px;
  padding: 5px 10px;
  margin: 0 10px;
}
.write-tab-active {
  color: #0066FF !important;
  border: 1px solid #0066FF !important;
}
.write-content {
  position: fixed;
  left: 0;
  top: 165px;
  bottom: 50px;
  width: 100%;
}
.write-content>div {
  padding: 0 10px;
  height: 100%;
}
.write-content>div>textarea {
  width: 100%;
  height: 100%;
  resize: none;
  outline:none;
  border: 0;
  font-size: 16px;
}
.write-footer {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 40px;
  width: 100%;
  border-top: 1px solid #e8e8e8;
}
.closeBtn,.postBtn {
  width: 60px;
  height: 26px;
  margin-top: 7px;
  color: #fff;
}
.closeBtn {
  background-color: coral;
  float: left;
  margin-left: 10px;
}
.postBtn{
  background-color: #5555FF ;
  float: right;
  margin-right: 10px;
}



