
.motif-top {
  padding: 0 10px;
  border-bottom: 1px solid #e8e8e8;
}
.a {
  padding-bottom: 200px;
}
.tab {
  color: #FFB3FF;
}
.time-tab {
  padding: 5px 0 10px 0;
}
.replies {
  padding: 0 10px;
}
.replies-cell {
  padding: 5px 0;
  width: 100%;
  overflow: hidden;
  border-bottom: 1px solid #e8e8e8;
  height: 100%;
}
.user-icon {
  width: 40px;
  height: 100%;
  float: left;
  border-radius: 5px;
}
.user-icon img {
  width: 100%;
  border-radius: 5px;
}
.replies-text {
  padding-left: 45px;
}
.author {
  font-size: 12px;
  padding: 2px 0 5px 0;
}
.author:after {
  content: attr(index);
  float: right;
  color: #88AA00;
}
.author >span {
  color: #9F88FF;
  font-size: 14px;
}
.replies-body {
  padding-bottom: 15px;
}
.collect-btn {
  padding-bottom: 15px;
}
.collect-btn button {
  width: 80px;
  padding: 8px 0;
  color: #fff;
}
.notCollect{
  background-color: #99BBFF;
}
.alrCollect{
  background-color: #0066FF ;
}
.fabulous {
  display: inline-block;
  padding-left: 23px;
}
.fabulous-icon {
  background: url("../assets/images/good.png") no-repeat 0 3px;
  background-size: 20px;
  color: #515151;
}
.onFabulous-icon {
  background: url("../assets/images/onGood.png") no-repeat 0 3px;
  background-size: 20px;
  color: #36ab60;
}
.replies-icon {
  display: inline-block;
  padding-left: 30px;
  background: url("../assets/images/replies.png") no-repeat 0 2px ;
  background-size: 25px;
}
.replies-footer span{
  line-height: 30px;
  height: 30px;
  width: 20px;
  text-align: left;
}
.author-icon {
  float: left;
  width: 40px;
  height: 40px;
}
.author-icon>img {
  width: 100%;
  border-radius: 4px;
}
.replies-box{
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 170px;
  background-color: #F8F8FF;
  transition: all .25s ease;
}
.textarea {
  padding: 10px 15px;
}
.textarea>textarea {
  width: 100%;
  border: 1px solid #e8e8e8;
  height: 95px;
  font-size: 16px;
  resize: none;
}
.replies-btn {
  padding: 0 30px;
}
.replies-btn>button {
  width: 70px;
  height: 30px;
  background-color: coral;
  color: #fff;
  font-size: 14px;
}
.replies-enter,.replies-leave {
  transform: translateY(0px);
}
.replies-enter-active,.replies-leave-active {
  transform: translateY(200px);
}
.replies-mask {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 200px;
}



